header{
    background-color: #0c66a6;
}
.fondo{
    background-color: #0c66a6;
}
.font{
    font-family: "raleway", sans-serif !important;
    font-weight: 600;
}
.font-warning{
    color: #ffce7a !important;
}
.font-gris{
    color: #777;
}
.font-gris2{
    color: #999;
}
.escala:hover{
    transform: scale(1.1);
    transition: all 300ms;
    cursor: pointer;
}
.cardAnimated:first-child{
    border-top: 4px solid #ffce7a;
    border-radius: 5px;
}
.cardAnimated{
    border-top: 4px solid #4da855;
    border-radius: 5px;
}
.cardAnimated:last-child{
    border-top: 4px solid #ce2900;
    border-radius: 5px;
}
.escalaCard:hover{
    border: 1px solid #fff;
    border-radius: 8px;
    background-color: #eef;
    cursor: pointer;
    padding-top: 10px;
    transform: scale(1.05);
    transition: all 300ms;
}


.sliderr{
    width: 100vw;
    height: 250px;
    overflow: hidden;
    position: relative;
    margin: auto;
    display: flex;
    margin-top: 50px;
    padding-top: 20px;
    padding-bottom: 20px;
}
.sliderr::before{
    top: 0;
    left: 0;
}
.sliderr::after{
    top: 0;
    right: 0;
    transform: rotateZ(360deg);
}
.sliderr .conte{
    display: flex;
    width: calc(250px * 24);
    animation: scroll 240s infinite linear;
}
.sliderr .cardMove{
    width: 350px;
    height: 210px;
    border-radius: 10px;
}
.cardMove:hover{
    transform: scale(1.1);
    cursor: pointer;
    border-radius: 10px;
    transition: all 300ms;
}

.sliderr .conte2{
    display: flex;
    width: calc(250px * 24);
    animation: scrollDos 150s infinite linear;
}


@keyframes scroll {
   0%{transform: translateX(-1600vw) ;}
   100%{transform: translateX(100vw);}
}
@keyframes scrollDos {
    0%{transform: translateX(50vw) ;}
    100%{transform: translateX(-700vw);}
 }



