.menu{
    width: 50px;
    height: 50px;
    background-color: #222;
    border-radius: 50%;
    overflow: hidden;
    position: fixed;
    right: 0;
    bottom: 0;
    margin-right: 30px;
    margin-bottom: 30px;
    text-align: center;
}
.menu:hover{
    width: 150px;
    height: 200px;
    background-color: #222;
    display: flex;
    flex-direction: column-reverse;
    border-radius: 5px;
    transition: all 300ms;
}
.btnMenu{
    font-size: 50px;
    position: relative;
    top: -10px;
}



/*  Modo Dark  */
.btnOnDark{
    background-color: rgb(22, 141, 81);
    color: #fff;
    border: 1px solid #aaa;
    border-radius: 5px;
    padding: 5px 0px;
    margin: 0 10px;
}
.backColorDark{
    background-color: #222;
    color: #fff;
    width: 100%;
    height: 100%;
    transition: all 200ms;
}


/*  Modo Light  */
.btnOnLight{
    background-color: #0ff;
    border: 1px solid #aaa;
    border-radius: 5px;
    padding: 5px 0px;
    margin: 0 10px;
}
.backColorLight{
    background-color: #fff;
    width: 100%;
    height: 100%;
    transition: all 200ms;
}

@media screen and (min-width: 600px) {
    .backColorDark{
        height: 100vh;
        transition: all 200ms;
    }
}