.imgGoogle{
    width: 100px;
    height: 100px;
    border-radius: 100%;
}
.imgGoogleTiny{
    width: 40px;
    height: 40px;
    border-radius: 100%;
}
.imgGoogleCard{
    width: 25px;
    height: 25px;
}
.font-size{
    font-size: 15px;
}
.bordesCard{
    border-radius: 20px;
    position: absolute;
    top: 120px;
    right: 2%;
}

.search{
    max-width: 600px;
    min-width: 320px;
    border-radius: 20px;
}
.fotter{
    width: 100vw;
    position: absolute;
    bottom: 0;
}
.font-gris{
    color: #777;
}


.anchoCardGoogle{
    position: absolute;
    top: 120px;
    right: 6%;
    max-width: 350px;
    min-width: 300px;
    max-height: 460px;
    border-radius: 20px;
}
.miniCardMenu:hover{
    cursor: pointer;
    border: 1px solid rgb(211, 205, 205);
    border-radius: 20px;
    background: rgb(210, 230, 255);
    transition: all 100ms;
}